html {
  background-color: #181818;
  color: #ffffff;
  margin: 0;
  padding: 0;
}

.App {
  font-family: 'Overpass';
  display: flex;
  flex-direction: column;
  height: 100vh;
  background-color: #181818;
  color: #ffffff;
}

.header {
  height: 270px;
  background-color: #181818;
  color: #ffffff;
}

.link {
  color: #8c8cb1;
  text-decoration: none;
  transition: color 0.3s;
  font-size: 18px;
}

.headertext {
  font-size: 18px;
  color: #8b8b8b;
}

.bodytext {
  margin-bottom: -10px;
}

.link:hover {
  color: #ccccee;
}

.link:active {
  color: #ccccee;
}

.logo {
  width: 200px;
  margin-top: 50px;
}

.main {
  flex: 1;
  display: flex;
}

@media only screen and (min-device-width : 768px) and (max-device-width : 1024px) {
  .sidebar {
    position: relative;
    float: left;
  }
}
/* imported: '/styles/media/mobile.scss */

@media only screen and (min-device-width : 320px) and (max-device-width : 480px) {
  .sidebar {
    position: inherit;
    float: left;
  }
}

.sidebar {
  flex: 1;
  width: 250px; /* Adjust this value as needed */
  margin-left: 50px; /* Adjust this value as needed */
  display: flex;
  flex-direction: column;
  background-color: #181818;
  position: fixed;
  overflow: auto;
  height: 100vh;
  z-index: 999;
}

hr.custom-hr {
  height: 1px; /* Adjust the height as desired */
  background-color: #444444; /* Adjust the color as desired */
  border: none; /* Remove the default border */
  margin: 40px 0; /* Adjust the margin as desired */
  width: 100px;
}

hr.custom-hr2 {
  height: 1px; /* Adjust the height as desired */
  background-color: #444444; /* Adjust the color as desired */
  border: none; /* Remove the default border */
  margin: 40px 0; /* Adjust the margin as desired */
  width: 640px;
}

.body {
  flex: 1;
  background-color: #181818;
  color: #ffffff;
}

.footer {
  height: 40px;
  margin-top: 40px;
  color: #494949;
  font-size: 12px;
  background-color: #181818;
  color: #535353;
}

.image-container {
  position: relative;
  display: inline-block;
}

.responsive-image {
  position: relative;
  width: 100%;
  height: auto;
}

.overlay {
  position: absolute;
  top: 0;
  bottom: 5px;
  left: 0;
  right: 0;
  transition: opacity 0.3s ease;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  cursor: pointer;
}

.overlay:hover {
  opacity: 1;
}

.text {
  color: white;
  font-size: 26px;
  text-align: center;
}

.description{
  color: white;
  font-size: 16px;
  text-align: center;
}

.responsive-image:hover .image-overlay {
  opacity: 1;
}


.image-grid {
  flex: 1;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 10px;
  padding: 20px;
  max-width: 820px; /* Adjust this value as needed */
  margin: 0;
  align-content: flex-start;
  justify-content: flex-start;
  background-color: #181818;
  overflow: auto; /* Enable scrolling within the image grid */
  margin-left: 300px;
}


@media (min-width: 481px) {
  .image-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

.project-image-grid {
  flex: 1;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px;
  padding: 20px;
  max-width: 620px; /* Adjust this value as needed */
  margin: 0;
  align-content: flex-start;
  justify-content: flex-start;
  background-color: #181818;
  margin-left: 300px;
}

@media (min-width: 481px) {
  .project-image-grid {
    grid-template-columns: repeat(1, 1fr);
  }
}

